import { h, Component } from 'preact';
import { Link } from 'preact-router/match';
import s from './style';

export default class Footer extends Component {
	render() {
		return (
			<footer class={s.footer}>
        <div class={s.copyright}>Copyright &copy; 2018 Codingscape LLC. All rights reserved.</div>
        <nav>
					<Link href="/contact" class={s.contact}>Contact Us</Link>
				</nav>
			</footer>
		);
	}
}



