import { h, Component } from 'preact';
import { Link } from 'preact-router/match';
import { Helmet } from 'react-helmet';
import s from './style';

export default class Header extends Component {

	constructor(props) {
		super(props);
		this.state = {
				cursorOn: false
		};
	}

	componentDidMount() {
		this.timer = setInterval(() => {
			this.setState({
				cursorOn: !this.state.cursorOn
			});
		}, 500);
	}

	componentWillUnmount() {
		if (this.timer) {
			clearInterval( this.timer );
		}
	} 

	render() {
		const cursor = this.state.cursorOn ? '_' : '';
		const cursorStyle = this.state.cursorOn ? { opacity: 1 } : { opacity: 0 };
		return (
			<header class={s.header}>
				<Helmet>
					<title>Codingscape{cursor}</title>
				</Helmet>
				<img class={s.logo} src="assets/cs-logo.svg" />
				<div class={s.subheader}>
					<h1 class={s.branding}>
						Codingscape
						<span class={s.cursor} style={cursorStyle}>_</span>
					</h1>
					<nav class={s.nav}>
						<Link activeClassName={s.active} href="/">Home</Link>
						<Link activeClassName={s.active} href="/capabilities">Capabilities</Link>
						<Link activeClassName={s.active} href="/about">About Us</Link>
						<Link activeClassName={s.active} href="/contact">Contact</Link>
					</nav>
				</div>
			</header>
		);
	}
}



